.container {
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}
body {
    background: #fafafa;
}
.right {
    text-align: right;
}

[class*=col-] {
    display: inline-block;
  }
  
  .col-lg-1 {
    width: 8.333333333%;
  }
  
  .col-lg-2 {
    width: 16.666666666%;
  }
  
  .col-lg-3 {
    width: 25%;
    padding:0;
  }
  
  .col-lg-4 {
    width: 33.3333333333%;
  }
  
  .col-lg-5 {
    width: 41.6666666666%;
  }
  
  .col-lg-6 {
    width: 50%;
  }
  
  .col-lg-7 {
    width: 58.33333333%;
  }
  
  .col-lg-8 {
    width: 66.666666666%;
  }
  
  .col-lg-9 {
    width: 75%;
  }
  
  .col-lg-10 {
    width: 83.33333333333%;
  }
  
  .col-lg-11 {
    width: 91.66666666666%;
  }
  
  .col-lg-12 {
    width: 100%;
  }
  
  @media (max-width: 400px) {
    .col-sm-1 {
      width: 8.333333333%;
    }
  
    .col-sm-2 {
      width: 16.666666666%;
    }
  
    .col-sm-3 {
      width: 25%;
    }
  
    .col-sm-4 {
      width: 33.3333333333%;
    }
  
    .col-sm-5 {
      width: 41.6666666666%;
    }
  
    .col-sm-6 {
      width: 50%;
    }
  
    .col-sm-7 {
      width: 58.33333333%;
    }
  
    .col-sm-8 {
      width: 66.666666666%;
    }
  
    .col-sm-9 {
      width: 75%;
    }
  
    .col-sm-10 {
      width: 83.33333333333%;
    }
  
    .col-sm-11 {
      width: 91.66666666666%;
    }
  
    .col-sm-12 {
      width: 100%;
    }
  }